	module.exports = {"data":{"remark":{"html":"<!-- **************************************** -->\n<section>\n<div class='container'>\n<div class='row'>\n<div class='col-xs-12'>\n<div class='hero-block'>\n<h2 id=\"graphile-build\"><a href=\"#graphile-build\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Graphile Build</h2>\n<h3 id=\"high-performance-pluggable-graphql-schema-tools\"><a href=\"#high-performance-pluggable-graphql-schema-tools\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>High-performance pluggable GraphQL schema tools</h3>\n<p>Prefer building your GraphQL APIs by hand? By using our <a href=\"/graphile-build/look-ahead/\">look-ahead\nfeature</a> your code can know what's coming and make\nsure it requests the correct fields from your data source ahead of time,\nleading to fewer round-trips and higher performance. Our <a href=\"/graphile-build/plugins/\">plugin\narchitecture</a> allows you to extend or enhance your\nGraphQL API as your needs evolve over time, and use community-built plugins to\nincrease developer productivity.</p>\n</div>\n</div>\n</div>\n<div class='row'>\n<div class='col-lg-6 mb3 col-md-9 col-xs-12'>\n<h5 id=\"graphql\"><a href=\"#graphql\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a><code class=\"language-text\">graphql</code></h5>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-js\"><code class=\"language-js\"><span class=\"token keyword\">const</span> MyType <span class=\"token operator\">=</span>\n<span class=\"gatsby-highlight-code-line\">  <span class=\"token keyword\">new</span> <span class=\"token class-name\">GraphQLObjectType</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">{</span>\n</span>    name<span class=\"token punctuation\">:</span> <span class=\"token string\">'MyType'</span><span class=\"token punctuation\">,</span>\n    fields<span class=\"token punctuation\">:</span> <span class=\"token punctuation\">{</span>\n      <span class=\"token comment\">// ...</span></code></pre>\n      </div>\n</div>\n<div class='col-lg-6 mb3 col-md-9 col-xs-12'>\n<h5 id=\"graphile-build-1\"><a href=\"#graphile-build-1\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a><code class=\"language-text\">graphile-build</code></h5>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-js\"><code class=\"language-js\"><span class=\"token keyword\">const</span> MyType <span class=\"token operator\">=</span>\n<span class=\"gatsby-highlight-code-line\">  <span class=\"token function\">newWithHooks</span><span class=\"token punctuation\">(</span>GraphQLObjectType<span class=\"token punctuation\">,</span> <span class=\"token punctuation\">{</span>\n</span>    name<span class=\"token punctuation\">:</span> <span class=\"token string\">'MyType'</span><span class=\"token punctuation\">,</span>\n    fields<span class=\"token punctuation\">:</span> <span class=\"token punctuation\">{</span>\n      <span class=\"token comment\">// ...</span></code></pre>\n      </div>\n</div>\n</div>\n<div class='flex'>\n<div>\n<p>Graphile Build is the core of PostGraphile - we recommend that you get started\nwith PostGraphile before you graduate to using Graphile Build directly.</p>\n<p><a class='strong-link' href='/postgraphile/'>Start with PostGraphile <span class='fa fa-fw fa-long-arrow-right' /></a>\n<a class='strong-link' href='/graphile-build/'>More about Graphile Build <span class='fa fa-fw fa-long-arrow-right' /></a></p>\n<div>\n</div>\n</div>\n</section>\n<!-- **************************************** -->\n<section>\nGraphQL training, pro services, etc\n</section>","frontmatter":{"path":"/","title":"Tools to build extensible and performant GraphQL APIs"}}},"pathContext":{"layout":"home"}}

